
@keyframes newTile{
from{transform:scale(0)
}
to{transform:scale(1)
}
}
.tile{position:absolute;width:110px;height:110px;margin:6px;line-height:90px;display:inline-block;font-size:55px;font-weight:bold;text-align:center;vertical-align:middle;border-radius:7px;font-family:"Clear Sans";color:#766;background-color:#dcb;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default
}
.tile.merged{display:none
}
.tile.merged.isMoving{display:inline
}
.tile.new{animation-duration:0.20s;animation-name:newTile;animation-fill-mode:forwards;animation-delay:0.15s;transform:scale(0)
}
.tile0{background-color:#dcb
}
.tile2{background-color:#eee
}
.tile4{background-color:#eec
}
.tile8{color:#ffe;background-color:#fb8
}
.tile16{color:#ffe;background-color:#f96
}
.tile32{color:#ffe;background-color:#f75
}
.tile64{color:#ffe;background-color:#f53
}
.tile128{color:#ffe;background-color:#ec7;font-size:45px
}
.tile256{color:#ffe;background-color:#ec6;font-size:45px
}
.tile512{color:#ffe;background-color:#ec5;font-size:45px
}
.tile1024{color:#fff;background-color:#ec3;font-size:35px
}
.tile2048{color:#fff;background-color:#ec2;font-size:35px
}



